EAChC,YAAY;EACZ,qBAAqB;EACrB,8BAA8B;EAC9B,kBAAkB;AACpB;;AAEA;EACE,oCAAoC;EACpC,mBAAmB;EACnB,qBAAqB;EACrB,YAAY;AACd;;AAEA;EACE,eAAe;EACf,+BAA+B;AACjC;;AAEA;EACE,aAAa;EACb,cAAc;AAChB;;AAEA,0CAA0C;;AAE1C;EACE,UAAU;AACZ;;AAEA;EACE,8BAA8B;EAC9B,2BAA2B;AAC7B;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,aAAa;AACf;;AAEA,iBAAiB;;AAEjB;EACE,cAAc;EACd,cAAc;AAChB;;AAEA;EACE,qCAAqC;EACrC;AACF;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;EAChB,iBAAiB;EACjB,qBAAqB;EACrB,cAAc;EACd,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;;EAEE,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,mCAAmC;AACrC;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,6BAA6B;AAC/B;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;EAClB,eAAe;AACjB;;AAEA,cAAc;;AAEd;EACE,aAAa;EACb,qCAAqC;EACrC,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,4BAA4B;EAC5B,gBAAgB;AAClB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,mBAAmB;EACnB,YAAY;EACZ,oCAAoC;EACpC,YAAY;EACZ,qBAAqB;EACrB,gBAAgB;AAClB;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,0BAA0B;EAC1B,6BAA6B;EAC7B,UAAU;AACZ;;AAEA;EACE,oBAAoB;AACtB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,oCAAoC;EACpC,YAAY;AACd;;AAEA;EACE,+BAA+B;AACjC;;AAEA;EACE,gBAAgB;AAClB","sourcesContent":["/* ==========================================================================\n  Reset \n  ========================================================================== */\n\n  *, *::before, *::after {\n    box-sizing: border-box;\n  }\n  * {\n    margin: 0;\n    padding: 0;\n  }\n  body {\n    line-height: 1.5;\n    -webkit-font-smoothing: antialiased;\n  }\n  img, picture, video, canvas, svg {\n    display: block;\n    max-width: 100%;\n  }\n  input, button, textarea, select {\n    font: inherit;\n  }\n  p, h1, h2, h3, h4, h5, h6 {\n    overflow-wrap: break-word;\n  }\n\n/* ==========================================================================\n  General\n  ========================================================================== */\n\n/* Colour Palette */\n\n* {\n  --purple: #94618E;\n  --dark-purple: #49274A;\n  --peach: #EBDAC8;\n  --off-white: #F8EEE7;\n  --brown: #261e19;\n  --priority-urgent: #b91c1c;\n  --priority-important: #c2410c;\n  --priority-low-priority: #f59e0b;\n  --task-completed: #0284c7;\n}\n\n/* https://www.fontsquirrel.com/fonts/roboto */\n\n@font-face {\n  font-family: \"Roboto\";\n  src: url(\"../assets/Roboto-Bold.ttf\") format(\"opentype\");\n  font-weight: normal;\n  font-style: normal;\n}\n\n/* Font Stack */\n/* https://modernfontstacks.com/ */\n\nbody {\n  font-family: \"Roboto\", Optima, Candara, 'Noto Sans', source-sans-pro, sans-serif;\n}\n\n/* Content */\n\nbody {\n  height: 100vh;\n  width: 100vw;\n  display: grid;\n  justify-items: center;\n  align-items: center;\n  background-color: var(--dark-purple);\n}\n\n#main {\n  height: 75vh;\n  width: 60vw;\n  display: grid;\n  grid-template-columns: 1fr 3.5fr;\n  background-color: var(--peach);\n  border-radius: 0.2rem;\n  padding: 1em;\n}\n\n/* ==========================================================================\n  Sidebar\n  ========================================================================== */\n\n#sidebar {\n  padding: 0.6em;\n  display: grid;\n  grid-template-rows: 1fr auto;\n  border-radius: 0.2rem;\n  background-color: var(--purple);\n  overflow-y: hidden;\n}\n\n#agenda-list-container {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 1em;\n  overflow-y: auto;\n}\n\n#agenda-list-container h2 {\n  font-size: 2em;\n  color: var(--peach);\n}\n\n#agenda-list {\n  list-style-type: none;\n  margin-bottom: 1em;\n  color: var(--brown);\n  overflow-y: auto;\n}\n\n/* Agenda Items */\n\n.agenda-item {\n  margin-bottom: 1em;\n  padding: 0.2em;\n  background-color: var(--peach);\n  display: flex;\n  align-items: center;\n  gap: 0.1em;\n}\n\n.agenda-item:hover {\n  background-color: var(--off-white);\n  cursor: pointer;\n  outline: 1px solid var(--brown);\n}\n\n.agenda-item.disabled-agenda-item {\n  opacity: 0.5;\n}\n\n.agenda-item.disabled-agenda-item:hover {\n  cursor: default;\n}\n\n.current-agenda {\n  background-color: var(--dark-purple);\n  color: var(--peach);\n}\n\n.agenda-item.current-agenda:hover {\n  background-color: var(--dark-purple);\n  outline-color: var(--off-white);\n}\n\n.current-agenda.agenda-editing {\n  outline: double var(--off-white);\n  outline-width: 0.3em;\n}\n\n.agenda-item h3 {\n  flex: 1;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.agenda-edit-button {\n  background-image: url(../assets/pencil-outline.svg);\n}\n\n.current-agenda .agenda-edit-button {\n  background-image: url(../assets/pencil-outline-white.svg);\n}\n\n.agenda-edit-button:hover {\n  background-image: url(../assets/pencil-outline-blue.svg);\n}\n\n.current-agenda .agenda-delete-button {\n  background-image: url(../assets/delete-outline-white.svg);\n}\n\n.agenda-delete-button {\n  background-image: url(../assets/delete-outline.svg);\n}\n\n.agenda-delete-button:hover {\n  background-image: url(../assets/delete-outline-red.svg);\n}\n\n.agenda-edit-save-button {\n  background-image: url(../assets/edit-save.svg);\n}\n\n.agenda-edit-save-button:hover {\n  background-image: url(../assets/edit-save-blue.svg);\n  }\n\n.agenda-edit-cancel-button {\n  background-image: url(../assets/cancel-edit.svg);\n}\n\n.agenda-edit-cancel-button:hover {\n  background-image: url(../assets/cancel-edit-red.svg);\n  }\n\n.agenda-edit-button, .agenda-delete-button, .agenda-edit-save-button,\n.agenda-edit-cancel-button {\n  background-repeat: no-repeat;\n  height: 1.2em;\n  width: 1.2em;\n}\n\n.agenda-item, #add-agenda {\n  font-size: 1.2em;\n  height: 2.2em;\n  width: 11em;\n  border-radius: 0.2rem;\n  border: none;\n}\n\n#add-agenda-container {\n  display: grid;\n  justify-items: center;\n}\n\n#add-agenda {\n  background-color: var(--dark-purple);\n  color: var(--peach);\n}\n\n#add-agenda:hover {\n  background-color: var(--peach);\n  color: var(--brown);\n  cursor: pointer;\n}\n\n/* ==========================================================================\n  Tasks\n  ========================================================================== */\n\n#tasks {\n  background-color: var(--peach);\n  border-radius: 0.2rem;\n  padding: 1em;\n  display: grid;\n  grid-template-rows: 1fr 4fr;\n  overflow: hidden;\n}\n\n/* Header */\n\n#agenda-header {\n  color: var(--brown);\n  display: grid;\n  grid-template-columns: 1fr auto;\n  grid-template-rows: 1fr auto auto;\n}\n\n#agenda-header h1 {\n  font-size: 3em;\n  grid-column: 1 / 2;\n  grid-row: 1 / 2;\n}\n\n#agenda-header h1.agenda-child-editing, #agenda-description.agenda-child-editing {\n  outline: 1px solid;\n}\n\n#agenda-header h1.agenda-child-editing {\n  width: 20ch;\n}\n\n#agenda-description.agenda-child-editing {\n  width: 60ch;\n}\n\n#agenda-header h1.agenda-child-editing.max-characters::after,\n#agenda-description.agenda-child-editing.max-characters::after {\n  content: \"Max. characters\";\n  font-size: 1rem;\n  position: absolute;\n  color: red;\n  right: 35em;\n}\n\n#agenda-description {\n  grid-column: 1 / 2;\n  grid-row: 2 / 3;\n}\n\n#priorities-key {\n  grid-column: 2 / 3;\n  grid-row: 1 / end;\n  place-self: center;\n  display: grid;\n}\n\n#priorities-key-list {\n  list-style-type: none;\n}\n\n#urgent-priority-key {\n  color: var(--priority-urgent);\n}\n\n#important-priority-key {\n  color: var(--priority-important);\n}\n\n#low-priority-key {\n  color: var(--priority-low-priority);\n}\n\n#tasks hr {\n  border-color: var(--brown);\n  grid-column: 1 / end;\n  grid-row: 3 / 4;\n}\n\n/* Task List */\n\n#task-list-container {\n  display: grid;\n  grid-template-rows: 1fr;\n  padding: 1em;\n  overflow-y: hidden;\n}\n\n#task-list {\n  list-style-type: none;\n  overflow-y: auto;\n  padding: 0 1em;\n}\n\n/* Task Items */\n\n.task-item, .expanded-task-item {\n  height: 3.5em;\n  border-radius: 0.2rem;\n  margin-bottom: 1em;\n  background-color: var(--purple);\n  color: var(--peach);\n}\n\n.expanded-task-item {\n  height: 8em;\n}\n\n.task-item.completed {\n  opacity: 0.5;\n}\n\n.task-item div {\n  height: 100%;\n  display: grid;\n  justify-items: center;\n  align-items: center;\n  grid-template-columns: 0.4fr 0.6fr 6fr repeat(2, 1.2fr) repeat(2, 0.4fr);\n  padding: 0 1em 0 0;\n}\n\n/* Task Item Children */\n\n.task-check-container {\n  display: grid;\n  align-items: center;\n}\n\n/* Checkbox from https://moderncss.dev/pure-css-custom-checkbox-style/ */\n\n.task-check {\n  -webkit-appearance: none;\n  appearance: none;\n  background-color: #fff;\n  margin: 0;\n  font: inherit;\n  color: currentColor;\n  width: 1.15em;\n  height: 1.15em;\n  border: 0.15em solid var(--dark-purple);\n  border-radius: 0.2rem;\n  display: grid;\n  place-content: center;\n}\n\n.task-check:hover {\n  cursor: pointer;\n}\n\n.task-check::before {\n  content: \"\";\n  width: 0.65em;\n  height: 0.65em;\n  transform: scale(0);\n  transition: 120ms transform ease-in-out;\n  box-shadow: inset 1em 1em var(--dark-purple);\n  background-color: CanvasText;\n  \n  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);\n}\n\n.task-check:checked::before {\n  transform: scale(1);\n}\n\ninput[type=\"checkbox\"]:focus {\n  outline: max(2px, 0.15em) solid currentColor;\n  outline-offset: max(2px, 0.15em);\n}\n\n/* ========== */\n\n.task-title {\n  width: 100%;\n  padding: 0 1em 0 0;\n  text-align: left;\n}\n\n.task-item.completed .task-title {\n  text-decoration: line-through;\n}\n\n/* ========== */\n\n.task-details-button:hover {\n  cursor: pointer;\n  background-color: var(--peach);\n  color: var(--purple);\n  padding: 0.2em;\n  border-radius: 0.2em;\n}\n\n/* ========== */\n\n.task-edit-button {\n  background-image: url(../assets/pencil-outline-white.svg);\n}\n\n.task-edit-button:hover {\n  cursor: pointer;\n  background-image: url(../assets/pencil-outline-blue.svg);\n}\n\n.task-item.completed .task-edit-button:hover {\n  cursor: default;\n  background-image: url(../assets/pencil-outline-white.svg);\n}\n\n/* ========== */\n\n.task-delete-button {\n  background-image: url(../assets/delete-outline-white.svg);\n}\n\n.task-delete-button:hover {\n  cursor: pointer;\n  background-image: url(../assets/delete-outline-red.svg);\n}\n\n/* ========== */\n\n.task-edit-button, .task-delete-button {\n  background-repeat: no-repeat;\n  height: 1.5em;\n  width: 1.5em;\n}\n\n/* ========== */\n\n.task-priority {\n  border-radius: 0.2rem 0 0 0.2rem ;\n  width: 100%;\n  height: 100%;\n  color: var(--peach);\n  display: grid;\n  place-content: center;\n}\n\n.task-priority.urgent {\n  background-color: var(--priority-urgent);\n}\n\n.task-priority.important {\n  background-color: var(--priority-important);\n}\n\n.task-priority.low {\n  background-color: var(--priority-low-priority);\n}\n\n/* ========== */\n\n#add-task-container {\n  display: grid;\n  justify-items: center;\n}\n\n#add-task {\n  font-size: 1.2em;\n  height: 2.2em;\n  width: 11em;\n  border-radius: 0.2rem;\n  border: none;\n  background-color: var(--dark-purple);\n  color: var(--peach);\n}\n\n#add-task:hover {\n  cursor: pointer;\n  background-color: var(--purple);\n}\n\n/* ========== */\n\n#add-task:disabled:hover, #add-agenda:disabled:hover {\n  background-color: var(--dark-purple);\n  cursor: default;\n}\n\n#add-task:disabled, #add-agenda:disabled {\n  opacity: 0.5;\n}\n\n/* ==========================================================================\n  Dialogs\n  ========================================================================== */\n\ndialog {\n  /*  https://medium.com/@debajyotim/making-an-element-center-simple-css-tricks-b51cca7597ce */\n  position: fixed;\n  left: 50%;\n  top: 50%;\n  transform: translate(-50%, -50%);\n  padding: 1em;\n  border-radius: 0.2rem;\n  border: 3px solid var(--brown);\n  text-align: center;\n}\n\ndialog button {\n  background-color: var(--dark-purple);\n  color: var(--peach);\n  border-radius: 0.2rem;\n  border: none;\n}\n\ndialog button:hover {\n  cursor: pointer;\n  background-color: var(--purple);\n}\n\n.dialog-con